.advantage {
    padding: 80px 0;


    &__block {
            justify-content: center
        


    }

    &__col {
        @include col(4);
        @include media($lg){
            @include col(6);
        }
        @include media($sm){
            @include col(8);
        }
        @include media($xs){
            @include col(10);
        }
        
        @include media($lg){
            margin-bottom: 40px;
        }
    }

    &__item {
        text-align: center;
        border: 1px solid $gray;
        border-radius: 5px;
        padding: 30px 10px;
        position: relative;
        // @include col-sm(6);

    }

    &__img {
        display: flex;
        justify-content: center;
        height: 120px;
        display: inline-block;
    }

    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__title {
        font-size: 18px;
        font-weight: 300;
        margin-top: 30px;
        z-index: 10;
        position: relative;
    }


    &__quest-bg {
        width: 90px;
        height: 90px;
        position: absolute;
        right: 0;
        bottom: 0;
        

    }

    &__quest-bg img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }




    &__arrow {
        width: 140px;
        height: 40px;
        margin:50px auto 0 auto;
        position: relative;

        @include media($lg){
            margin-top: 0;
        }
    }

    &__arrow-item::after,&__arrow-item::before {
        content: '';
        display: block;
        width: 80px;
        height: 2px;
        background: $gray;
        position: absolute;

    }

    &__arrow-item::before {
        left: 0;
        top: 0;
        transform: rotate(40deg);
    }
    &__arrow-item::after {
        right: 0;
        top: 0;
        transform: rotate(-40deg);
    }

    &__arrow-second {
        &::after,&::before{
            top: 22px;

        }
    }

    &__title-block {
        margin-top: 20px;
        margin-bottom: 30px;
    }

    &__title-block h2 {
        font-size: 48px;
        font-weight: 700;
        text-align: center;

        @include media($lg){
            font-size: calc(25px + (48 - 25)*((100vw - 320px)/(1170 - 320)));
        }

    }


}