.start {
    padding: 75px 0;

    &__body {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__logo {
        margin: auto;
        width: 300px;
        // height: 300px;

        @include media($lg){
            width: calc(200px + (300 - 200)*((100vw - 320px)/(1170 - 320)));
        }

    }

    &__logo img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__title {
        text-align: center;
        font-size: 30px;
        font-weight: 900;
        margin-top: 75px;

        @include media($lg){
            font-size: calc(20px + (30 - 20)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__link {
        display: inline-block;
        background: $blue;
        padding: 20px 40px;
        text-transform: uppercase;
        color: $white;
        margin: auto;
        margin-top: 30px;
        border-radius: 5px;
    }

}