.works {
    padding: 75px 0;
    background: url('../images/works-bg.png') center center / cover no-repeat;


    &__title {
        text-align: center;
        color: $white;
        font-size: 30px;
        @include media($lg){
            font-size: calc(15px + (30 - 15)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__nav {
        border: 1px solid $white;
        border-radius: 5px;
        width: max-content;
        margin: 30px auto;
        overflow: hidden;
        overflow-x: scroll;

        @include media($lg){
            width: calc(250px +  (700 - 250)*((100vw - 320px)/(1170 - 320)));
        }
        
    }
    
    &__list {
        display: flex;
        height: 63px;
        align-items: center;

        @include media($lg){
            height: calc(40px + (63 - 40)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__link {
        padding: 0 30px;
        color: $white;
        text-align: center;
        display: inline-block;
        font-size: 14px;

        @include media($lg){
            padding: 0 calc(15px + (30 - 15)*((100vw - 320px)/(1170 - 320)));
            font-size: calc(10px + (14 - 10)*((100vw - 320px)/(1170 - 320)));
        }
    }


    &__item {
        width: 940px;
        margin: auto;

        @include media($lg){
            width: calc(250px + (940 - 250)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__item img {
        width: 100%;
        
    }

}