  }
}

.nav__burger {
  display: none;
  height: 20px;
  width: 30px;
  position: relative;
  z-index: 10;
}

.nav__burger.active::before {
  top: 9px;
  transform: rotate(45deg);
}

.nav__burger.active::after {
  bottom: 9px;
  transform: rotate(-45deg);
}

.nav__burger.active span {
  display: none;
}

@media (max-width: 768px) {
  .nav__burger {
    display: block;
  }
}

.nav__burger span {
  width: 100%;
  height: 1px;
  background: white;
  position: absolute;
  left: 0;
  top: 9px;
}

.nav__burger::after, .nav__burger::before {
  content: '';
  width: 100%;
  height: 1px;
  background: white;
  position: absolute;
  left: 0;
  transition: 0.5s;
}

.nav__burger::before {
  top: 0;
}

.nav__burger::after {
  bottom: 0;
}

.nav__menu {
  z-index: 10;
}

@media (max-width: 768px) {
  .nav__menu {
    position: fixed;
    width: 100%;
    height: 100%;
    left: -100%;
    top: 0%;
    background: rgba(0, 0, 0, 0.7);
    transition: 0.5s;
  }
  .nav__menu.active {
    left: 0;
  }
}

.nav__link {
  color: white;
  font-size: 13px;
  text-transform: uppercase;
  margin-left: calc(20px + (45 - 20)*((100vw - 320px)/(1170 - 320)));
