.base {
    padding-top: 75px;
    background: url('../images/base.png') center center / cover no-repeat;
    // height: 580px;
    padding-bottom: 180px;

    @include media($lg){
        // height: calc(200px + (580 - 200)*((100vw - 320px)/(1170 - 320)));
        padding-top: calc(15px + (75 - 15)*((100vw - 320px)/(1170 - 320)));
        padding-bottom: calc(50px + (180 - 50)*((100vw - 320px)/(1170 - 320)));

    }

    @include media($sm){
        display: none;
    }


    &-main__title {
        text-align: center;
        font-size: 30px;
        font-weight: 900;
        color: $white;

        @include media($lg){
            font-size: calc(20px + (30 - 20)*((100vw - 320px)/(1170 - 320)));
        }

        @include media($sm){
            color: $black-font;
        }
        

    }

    &-container {
        @include media($sm){
            display: none;
        }
    }

    &__info {
        display: flex;
        justify-content: center;
        align-items: start;
        color: $white;
        margin-top: 130px;


        @include media($lg){
            margin-top: calc(50px + (130 - 50)*((100vw - 320px)/(1170 - 320)));
        }

        @include media($sm){
            flex-direction: column;
            align-items: center;
            
        }

    }

    &__title {
        font-size: 30px;
        font-weight: 900;
        margin-bottom: 18px;
        z-index: 10;
        position: relative;

        @include media($lg){
            font-size: calc(20px + (30 - 20)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__text {
        font-size: 18px;
        font-weight: 300;
        z-index: 10;
        position: relative;

        @include media($lg){
            font-size: calc(14px + (18 - 14)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__item {
        width: 50%;
        position: relative;

        @include media($sm){
            color: $black-font;
        }

        // @include media($)

    }

    &-save__title {
        text-align: right;
    }

    &-save__text {
        text-align: right;
    }

    &-save {
        padding-right: 80px;

        @include media($lg){
            padding-right: calc(30px + (80 - 30)*((100vw - 320px)/(1170 - 320)));
        }

        @include media($sm){
            background: url('../images/option-1.png');
        }
    }

    &-server {
        padding-left: 80px;

        @include media($lg){
            padding-left: calc(30px + (80 - 30)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &__item::after{
        content: '';
        position: absolute;
        width: 170px;
        height: 170px;
        background: $blue-bg;
        right: 45px;
        top: -30px;

        @include media($lg){
            width: calc(80px + (170 - 80)*((100vw - 320px)/(1170 - 320)));
            height: calc(80px + (170 - 80)*((100vw - 320px)/(1170 - 320)));
            right: calc(20px + (45 - 20)*((100vw - 320px)/(1170 - 320)));
        }
    }

    &-server:after{
        left: 45px;

        @include media($lg){
            left: calc(20px + (45 - 20)*((100vw - 320px)/(1170 - 320)));
        }
    }



}



.transform {
    display: none;

    @include media($sm){
        display: block;
    
    }

    color: $white;

    &__base {
        background: url('../images/option-1.png') center center / cover no-repeat; 
    }
    &__server {
        background: url('../images/option-2.png') center center / cover no-repeat;
    }

    &-main__title {
        color: $black-font;
        text-align: center;
        margin-bottom: 20px;
    }

    &-item {
        padding: 80px 30px;

        @include media($xs){
            text-align: center;
        }

        @include media($sm){
            padding: calc(40px + (80 - 40)*((100vw - 320px)/(768 - 320))) 30px;
        }
    }

}