.advert {


    &__body {
        display: flex;
        justify-content: space-between;
        padding: calc(10px + (20 - 10) * ((100vw - 320px)/(1170 - 320))) 0;

        
    }

    &__item {
        display: flex;
        max-width: 230px;

        @include media($md){
            display: none;
        }
    }

    &__img {
        margin-right: 10px;
        width: 28px;
        height: 21px;
    }

    // &__img img {
    //     width: 100%;
    //     height: 100%;
    //     object-fit: cover;

    // }

    &__title {
        line-height: 15px;
        margin-bottom: 5px;
        font-size: 14px;
        color: $black-font;
    }

    &__text {
        font-size: 12px;
        color: $black-font;

    }

    &__link {
        display: none;
        margin: auto;

        @include media($md){
            display: block;
        }
    }

    &__link a {
        display: inline-block;
        padding: calc(5px + (10 - 5)*((100vw - 320px)/(1170 - 320))) calc(20px + (30 - 20)*((100vw - 320px)/(1170 - 320)));
        font-size: 14px;
        background: $blue-bg;
        color: $white;
    }
}