.banner {
    // height: 90vh;
    background: url('../images/banner-bg.png') center center / cover no-repeat;
}


.nav {
    height: 140px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include media($lg){
        height: calc(80px + (140 - 80)*((100vw - 320px)/(1170 - 320)));
    }



    &__list {
        display: flex;

        @include media($sm){
            padding-top: 25vh;
            flex-direction: column;
            justify-content: center;
            text-align: center;


        }
    }



    &__burger {
        display: none;
        height: 20px;
        width: 30px;
        position: relative;
        z-index: 10;

        &.active::before {
            top: 9px;
            transform: rotate(45deg)
        }
        &.active::after {
            bottom: 9px;
            transform: rotate(-45deg)
        }

        &.active span{
            display: none;
        }

        @include media($sm){
            display: block;
        }


        span {
            width: 100%;
            height: 1px;
            background: $white;
            position: absolute;
            left: 0;
            top: 9px;
        }
        

        &::after,&::before {
            content: '';
            width: 100%;
            height: 1px;
            background: $white;
            position: absolute;
            left: 0;
            transition: 0.5s;
        }



        &::before {
            top: 0;

        }

        &::after {
            bottom: 0;

        }
        

    }

    &__menu {
        z-index: 10;
        @include media($sm){
            position: fixed;
            width: 100%;
            height: 100%;
            left: -100%;
            top: 0%;
            background: $dark-bg;
            transition: 0.5s;

            &.active {
                left: 0;
            }
        }
    }

    &__link {
        color: $white;
        font-size: 13px;
        text-transform: uppercase;
        margin-left: calc(20px + (45 - 20)*((100vw - 320px)/(1170 - 320)));
        font-weight: 700;
        @include media($sm) {
            font-size: calc(15px + (25 - 15)*((100vw - 320px)/(768 -320)));
            display: inline-block;
            margin-bottom: 15px;
            
            
        }
    }


    &__logo img{
        max-width: 150px;
        
        @include media($lg){
            width: calc(90px + (145 - 70)*((100vw - 320px)/(1170 - 320)));
        }
    }
}

