.carousel {
    // flex-grow: 1;
    // display: flex;
    position: relative;


    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        width: 100%;
        z-index: 0;
        
        @include media($sm){
            flex-direction: column;
            padding-bottom: 80px;
        }

    }


    &__info {
        @include col(6);


        @include media($sm){
            @include col(10);
            text-align: center;
            margin-bottom: 40px;
        }
        
        @include media($xs){
            margin-top: 60px;

        }
    }

    &__title {
        font-size: 42px;
        font-weight: 700;
        color: $white;
        margin-bottom: 25px;

        @include media($lg){
            font-size: calc(20px + (42 - 20)*((100vw - 320px) / (1170 - 320)));
        }
    }

    &__text {
        color: $white;
        font-size: 24px;
        line-height: 30px;
        font-weight: 300;

        @include media($lg){
            font-size: calc(16px + (24 - 16)*((100vw - 320px) / (1170 - 320)));
        }
    }

    &__link {
        padding: 20px 50px;
        background: $blue;
        display: inline-block;
        color: $white;
        text-transform: uppercase;
        font-size: 16px;
        border-radius: 5px;
        margin-top: 40px;

        @include media($lg){
            padding: calc(10px + (20 - 10)*((100vw - 320px) / (1170 - 320))) calc(20px + (50 - 20)*((100vw - 320px) / (1170 - 320)));
            text-align: center;
        }

        
    }

    &__img {
        overflow: hidden;
        max-width: 430px;

        @include media($lg){
            width: calc(150px + (430 - 150) * ((100vw - 320px) / (1170 - 320)));
        }

    }

    &__img img {
        height: 100%;
        width: 100%;
    }
      
}

.slick-list {
    overflow: hidden;
}

.slick-track {
    display: flex;
}

.slick-arrow {
    position: absolute;
    bottom: 0;
    left: 47%;
    font-size: 0;
    border: none;
    background: none;
    // z-index: 10;
}

