$Montserrat:('Roboto','Regular', 400),
            ('Roboto','Light', 300),
            ('Roboto','Bold', 700),
            ('Roboto','Black', 900),
            ('fontawesome','duotone', 900),
            ('fontawesome','light', 300),
            ;

@each $family, $name, $weight in $Montserrat {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}




$white: white;
$black: black;
$black-font: #242424;
$dark-font: #393939;
$gray: rgba(190, 190, 190, 0.603);
$blue: #00c2f0;
$blue-bg: rgba(0, 192, 240, 0.5);
$dark-bg: rgba(0, 0, 0, 0.7);
$secondDark-bg: rgba(0, 0, 0, 0.3);
$footer-bg: #0082a1;
