.plus {
    padding: 80px 0;

    &__title {
        font-size: 30px;
        margin-bottom: 50px;
        text-align: center;
        font-weight: 900;

        @include media($lg){
            font-size: calc(20px + (30 - 20)* ((100vw - 320px)/(1170 - 320)));
        }
    }

    &__section {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        height: 355px;
        width: 50%;
        display: flex;
        align-items: center;



        @include media($lg){
            height: calc(200px + (355 - 200)*((100vw - 320px)/(1170 - 320)));
        }

        @include media($xs){
            width: 100%;

        }
    }

    &__info {
        background: $secondDark-bg;
        width: 100%;
        text-align: center;
        height: 157px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 10px;

        @include media($lg){
            height: calc(100px + (157 - 100)*((100vw - 320px)/(1170 - 320)));
        }
    }
    
    &__info h2{
        font-size: 23px;
        max-width: 340px;
        color: $white;
        font-weight: 400;

        @include media($lg){
            font-size: calc(16px + (23 - 16) * ((100vw - 320px) / (1170 - 320)));
        }
    }

    &__item:nth-child(1){
        background: url('../images/plus-1.png') center center / cover no-repeat;
    }
    &__item:nth-child(2){
        background: url('../images/plus-2.png') center center / cover no-repeat;
    }
    &__item:nth-child(3){
        background: url('../images/plus-3.png') center center / cover no-repeat;
    }
    &__item:nth-child(4){
        background: url('../images/plus-4.png') center center / cover no-repeat;
    }
    &__item:nth-child(5){
        background: url('../images/plus-5.png') center center / cover no-repeat;
    }
    &__item:nth-child(6){
        background: url('../images/plus-6.png') center center / cover no-repeat;
    }


}